import React, { useEffect, memo } from 'react';
import { Table } from 'react-bootstrap';
import { __SECRET_INTERNALS_DO_NOT_USE_OR_YOU_WILL_BE_FIRED } from 'react-dom/cjs/react-dom.development';
import { connect } from 'react-redux';


function Cart(props) {
    return (
        <div>
            <Table striped bordered hover variant="dark">
                <tr>
                    <th>#</th>
                    <th>상품명</th>
                    <th>분류</th>
                    <th>수량</th>
                    <th>변경</th>
                </tr>
                {

                    props.state.map(function (a, i) {
                        return (
                            <tr key={i}>
                                <td>{[i + 1]}</td>
                                <td>{a.name}</td>
                                <td>{a.id}</td>
                                <td>{a.quan}</td>
                                <td><button onClick={() => { props.dispatch({ type: '수량증가', 데이터 : a.id  } ) }}>+</button>       
                                    <button onClick={() => { props.dispatch({ type: '수량감소', 데이터 : a.id  } ) }}>-</button></td>
                            </tr>
                        )
                    })

                }
            </Table>
            {    props.alert열림 === true

                ? <div className="my-alert2">
                    <p>지금 구매하시면 신규할인 20%</p>
                    <button onClick={()=>{ props.dispatch({ type: '닫기' }) }}>닫기</button>
                   </div>
                : null
            }
            <Parent 이름="승환" 나이="25"></Parent>

        </div>
    )
}


function Parent(props){
    return(
        <div>
            <Child1 이름 = {props.이름} ></Child1>
            <Child2 나이 = {props.나이} ></Child2>
        </div>
    )
}

function Child1(props){
    useEffect(()=>{ console.log('랜더링됨1')});
    return <div>11111</div>
}
let Child2 = memo(function(){
    useEffect(()=>{ console.log('랜더링됨2')});
    return <div>2222</div>
})
;







